.player {
  display: grid;
  column-gap: 7px;
  grid-template-columns: 1fr 3fr repeat(9, 1fr);
  grid-template-rows: 51px;
  align-items: center;
  &.grey {
    background: rgb(41, 41, 41);
  }
  div {
    font-size: 13px;
  }
}

.activeId {
  color: rgb(126, 255, 126);
  cursor: pointer;
}

.netWorth {
  color: rgb(255, 205, 66);
}

.heroImg {
  position: relative;
  img {
    height: 100%;
    display: block;
  }
  div {
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 1px 4px;
  }
}
