@mixin mobile() {
  @media (max-width: 414px) {
    @content;
  }
}

@mixin tablet() {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin desktop() {
  @media (min-width: 1210px) {
    @content;
  }
}

@mixin desktop-lg() {
  @media (min-width: 1480px) {
    @content;
  }
}
